<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Sharing Model</h1>
      <div class="chartWrap">
        </div>
    </section>
    
    <div class="tabWrap">
        <van-tabs v-model="active" title-active-color="#f44" @click="onClick">
            <van-tab title="Complete Pic">
                <ul>
                    <li>
                        <div class="adsWrap">
                            <div class="userheadWrap">
                                <div class="userHead">
                                     <img :src="simple.headImage">
                                </div>
                                <div class="nameWrap">
                                    <p>{{simple.name}}</p>
                                    <span>{{simple.beginTime}} </span>
                                </div>
                            </div>
                            <div class="connect">
                                {{simple.content}}
                            </div>
                            <div class="content">
                                <img :src="simple.img">
                            </div>

                        </div>   
                        <van-button type="danger" @click="simpleShare">Share by This Model</van-button>   
                    </li>
                </ul>
                
            </van-tab>
            <van-tab title="Multiple Pics">
 <ul>
                    <li>
                        <div class="adsWrap">
                            <div class="userheadWrap">
                                <div class="userHead">
                                     <img :src="multi.headImage">
                                </div>
                                <div class="nameWrap">
                                    <p>{{multi.name}}</p>
                                    <span>{{multi.beginTime}} </span>
                                </div>
                            </div>
                            <div class="connect">
                                {{multi.content}}
                            </div>
                            <div class="content">
                                <div class="photoWrap">
                                    <div class="photo_top">
                                        <div class="photo_left" v-if="multi.imgList&&multi.imgList.length>0">
                                            <img :src="multi.imgList[0].img">
                                        </div>
                                        <div class="photo_right" v-if="multi.imgList&&multi.imgList.length>1">
                                            <img :src="multi.imgList[1].img">
                                        </div>
                                    </div>
                                    <div class="photo_btm">
                                        <div class="photoList mr9" v-if="multi.imgList&&multi.imgList.length>2">
                                            <img :src="multi.imgList[2].img">
                                        </div>
                                        <div class="photoList mr9" v-if="multi.imgList&&multi.imgList.length>3">
                                            <img :src="multi.imgList[3].img">
                                        </div>
                                          <div class="photoList mr9" v-if="multi.imgList&&multi.imgList.length>4">
                                            <img :src="multi.imgList[4].img">
                                        </div>
                                        <!-- <div class="tail">
                                            <span>+6</span>
                                        </div> -->
                                    </div>
                                </div>
                            </div>

                        </div>   
                        <van-button type="danger" @click="multiShare">Share by This Model</van-button>   
                    </li>
                </ul>

            </van-tab>
            
        </van-tabs>
        
     </div>
 
  </div>
</template>

<script>

import {shareActivityMaterial,shareActivityMaterialMultiImg} from '@/api/flashSale'
export default {
  name: 'shanringModel',
  data () {
    return {
        value:'',
        active: 0 ,
        id:this.$route.query.id,
        shareVisible: false,
        simple:{},
        multi:{}
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
        shareActivityMaterial({id:this.id}).then(res=>{
            this.simple=res.data.result;
        });
         shareActivityMaterialMultiImg({id:this.id}).then(res=>{
            this.multi=res.data.result;
        });
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    onClick(index){
      
    },
    simpleShare(){
            this.$store.dispatch('share',
                {
                "imageUrl": [this.simple.img],//????
                "url": "",//????
                "title": this.simple.content,
                "needPic":true
            }
            );
    },

    multiShare(){
            this.$store.dispatch('share',
                {
                "imageUrl":[this.multi.img],//????
                "url": "",//????
                "title": this.multi.content,
                "needPic":true
            }
            );
    }


  }
}
</script>

<style lang="scss" >
    .tabWrap{
        .van-tabs__line{
            width: 21px !important;
            left: 33px !important;
        }
    }
</style>
<style lang="scss" scoped>
    .tabWrap{
        .van-tab{
           flex-basis: 0 !important;
           min-width: auto;
           padding: 0 15px;
        }
        .van-tabs__line{
            width: 21px;
        }
        li{
            border-top: 10px solid #f6f6f6;
        }
    }
   
    .adsWrap{
        padding: 0 4%;
    }
    .userheadWrap{
        width: 100%;
        display: flex; align-items: center;
        padding: 10px 0;
        .nameWrap{
            p{
                font-size: 14px;
                color: #333333;
            }
            span{
                font-size: 12px;
                color: #666666;
            }
        }
    }
    .userHead{
         margin-right: 10px;
         width: 40px;
         height: 40px;
        img{
            width: 100%;
            height: 100%;    
        }
    }
    .connect{
        width: 100%;
        word-break: break-all;
        word-wrap: break-word;
        font-size: 14px;
        color: #333333;
        a{
            color: #2B87D5;
        }
    }
    .content{
        p{
            color: #2B87D5;
            padding: 2px 0 10px 0;
            font-size: 13px;
            font-weight: 500;
        }
    }
    .photo_top{
        display: -webkit-box;
    }
    .photo_left{
        width: 166px;
        height: 102px;
        margin-right: 12px;
        img{
            width: 100%;
            height: 100%;
        }
    }
    .photo_right{
        width: 166px;
        height: 102px;
        img{
            width: 100%;
            height: 100%;
        }
    }
    .photo_btm{
        display: -webkit-inline-box;
        margin-top: 10px;
    }
    .photoList{
        width: 109px;
        height: 106px;
        
        img{
            width: 100%;
            height: 100%;
        }
    }
    .mr9{
        margin-right: 9px;
    }
    .van-button--danger{
        width: 92%;
        margin-left: 4%;
        margin-top: 35px;
    }
    .van-button--danger:first-child{
        margin-right: 8px;
        background-color: #b20808;
    }
</style>



